//@flow

import map from 'lodash/fp/map'
import filter from 'lodash/fp/filter'
import reduce from 'lodash/fp/reduce'
import uniq from 'lodash/fp/uniq'
import compose from 'lodash/fp/compose'
import curry from 'lodash/fp/curry'

type WorkUrl = {
  '-type':'small|medium|large',
  '#text':string
}

type WorkExif = {
  model?:string,
  make?:string
}

type Work = {
  id:string,
  filename:string,
  image_width:number,
  image_height:number,
  urls:{
    url:Array<WorkUrl>,
  },
  exif: WorkExif
}

type Make = {
  [make:string]:{
    allModels:Array<String>
  }
}

type Model = {
  [model:string]:{
    allIds:Array<string>
  }
}

const makeExists = (work:Work) : boolean => work.exif.make != null
const pluckMake = (work:Work) :?string =>  {
  if(work.exif.make != null) {
    return  work.exif.make
  }
}

const modelExists = (work:Work) : boolean => work.exif.model !=null
const pluckModel = (work:Work) : ?string => {
  if(work.exif.model!=null) {
    return work.exif.model
  }
}

const getAllMakes = compose(
  uniq,
  map(pluckMake),
  filter(makeExists)
)

const getAllModels = compose(
   uniq,
   map(pluckModel),
   filter(modelExists)
)

/*
  get a list of all makes
  for each make,
 */

const extractModels = (works:Array<Work>) => {
  const allModels:Array<Work> = getAllModels(works)
  const filterByModel = curry((model:?string,work:Work):boolean => {
    if(work.exif.model != null) {
      return work.exif.model === model
    }
    return false
  })

  const aggregateIdsByModel = (state = {},{ id, exif: { model = 'none' } } : { id: string, exif:{ model:string } } ) => {
    if(state[model] == null) {
      state[model] = {}
    }
    if(state[model].allIds == null) {
      state[model].allIds = [id]
    } else {
      state[model].allIds = [...state[model].allIds,id]
    }
    return state
  }

  const byModel = (model:string = 'none') => {
   const getForModels = compose (
      reduce(aggregateIdsByModel,{}),
      filter(filterByModel(model))
  )
   return getForModels(works)
 }

  const resolveModels = compose(
    uniq,
    map(byModel)
  )

  const models = resolveModels(allModels)
  return Object.assign.apply({},getAllModels(allModels))
}

const getByMake = curry((make:?string,work:Work):boolean => {
  if(work!=null && work.exif !=null && work.exif.make != null) {
    return work.exif.make === make
  }
  return false
})

